  overflow-x: hidden;
} */

.sidenav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: fixed;
  right: 0px;
  top: 0px;
  height: 100%;
  width: 60px;
  background: #333;
  z-index: 10000;
  @media (max-width: 480px) {
    display: none;
  }
}

.sidenav .nav {
  position: relative;
  display: flex;
  align-items: center;
  z-index: 1000;
  left: 0px;
  transition: all 0.4s ease;
  cursor: pointer;
}

.sidenav .nav.active {
  left: -10px;
}

.sidenav .nav:hover {
  left: -85px; /* Border width*2 */
}

.sidenav .description,
.sidenav .icon {
  height: 60px;
  line-height: 60px;
  background: #333;
}

.sidenav .nav.active .description,
.sidenav .nav.active .icon {
  background: rgb(100, 173, 82);
}

.sidenav .icon {
  width: 60px; /* Width of navbar */
  text-align: center;
  color: #e9e9e9;
  font-size: 25px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.sidenav .description {
  position: relative;
  left: 60px;
  color: #eee;
  min-width: 80px;
  padding-left: 10px;
  height: 100%;
}

.content {
  position: absolute;
  left: 0px;
  top: 0px;
  right: 60px; /* Width of navbar */
  height: 100%;
  z-index: 10;
  background: #ddd;
}
