/* NewGallery.css */
body {
  background-color: #ecf0f1;
}

.gallery-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: #fff;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.3);
  width: 80%;
  margin: 0 auto;
  padding: 10px;
}

.gallery-item {
  flex-basis: 32.7%;
  margin-bottom: 6px;
  opacity: 0.85;
  cursor: pointer;
  transition: opacity 0.3s ease;
}

.gallery-item:hover {
  opacity: 1;
}

.gallery-item img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.lightbox {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
}

.lightbox-content {
  position: relative;
  max-width: 70%;
  max-height: 80%;
}

.lightbox-content img {
  border-radius: 7px;
  box-shadow: 0 0 8px 0 rgba(225, 225, 225, 0.25);
  width: 100%;
  height: auto;
  object-fit: contain;
}

.lightbox-prev,
.lightbox-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 10px 15px;
  cursor: pointer;
  font-size: 24px;
}

.lightbox-prev {
  left: -45px;
}

.lightbox-next {
  right: -45px;
}

.lightbox-prev:hover,
.lightbox-next:hover {
  background-color: rgba(0, 0, 0, 1);
}

/* Responsive adjustments */
@media (max-width: 767px) {
  .gallery-container {
    width: 100%;
  }
  .gallery-item {
    flex-basis: 49.8%;
  }
}

@media (max-width: 480px) {
  .gallery-item {
    flex-basis: 100%;
  }
  .lightbox-content {
    max-width: 90%;
    max-height: 90%;
  }
}
